* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
  background-color: #f5f5f5;
  height: 200vh;
  overflow: hidden;
}

.App .top {
  display: flex;
  align-items: center;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  height: 50px;
}

.center {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.App .top .title {
  margin: 0 20px;
  display: flex;
  align-items: center;
  justify-self: center;
  user-select: none;
}

.App .top .title span {
  font-family: 'Hahmlet', sans-serif;
  font-size: 30px;
  font-weight: 100;
  color: #333;
}

.App .top .title img {
  height: 40px;
  margin: 0 5px 0 0;
}

.profile {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 0 20px;
  background-color: #f5f5f5;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ddd;
  width: 150px;
  cursor: pointer;
  user-select: none;
}

.profile img {
  border-radius: 20%;
  width: 30px;
  height: 30px;
}

.profile hr {
  margin: 0 5px;
  height: 30px;
  border: none;
  border-right: 1px solid #ddd;
}

.profile .info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-size: 12px;
}

.App .nav {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333333;
  color: #fff;
  height: 50px;
}

.App .nav .center{
  margin: 0 50px;
}

.App .nav li {
  list-style: none;
  margin: 0 10px;
  cursor: pointer;
  user-select: none;
}

.background {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* 반투명한 검은색 */
  z-index: 1;
}

.background iframe {
  height: 1200px;
  width: 2000px;
  margin: 0 auto;
  overflow-x: hidden;
  position: relative;
  bottom: 98px;
  z-index: -1;
  overflow: hidden;
  object-fit: cover;
  z-index: -1;
}

.App .body {
  position: absolute;
  top: 100px;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1;
}

.App .body .title {
  position: absolute;
  top: 400px;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
  user-select: none;
  white-space: nowrap;
}

.App .body .title h1 {
  font-size: 200px;
  font-weight: bold;
  padding: 0;
  text-align: center;
}

.App .body .title h2 {
  font-size: 30px;
  font-weight: 100;
  margin: 0;
  padding: 0;
}

.App .body .title button {
  margin-top: 50px;
  padding: 10px 20px;
  border: 2px solid #fff;
  border-radius: 40px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.6);
  font-size: 20px;
  cursor: pointer;
  user-select: none;
}

.App .body .title button:hover {
  background-color: rgba(0, 0, 0, 0.9);
}